import styled from 'styled-components';
import { H5 } from '../common/Typography';
import { useMedia } from 'react-use';

interface CardType {
  width: string;
}

const CardWrapper = styled.div<CardType>`
  width: ${(props) => props.width};
  display: flex;
  flex-flow: column;
  align-items: center;
  margin-bottom: 30px;

  video,
  img {
    width: 300px;
    height: 300px;
    border-radius: 25px;
    margin-bottom: 30px;
    &.md-license-image {
      object-fit: cover;
    }
    @media screen and (max-width: 768px) {
      width: 300px;
      height: 300px;
      margin-bottom: 24px;
    }
    @media screen and (max-width: 425px) {
      width: 150px;
      height: 150px;
      margin-bottom: 25px;
    }
  }

  .divider {
    height: 1px;
    width: 300px;
    background: #fff;

    @media screen and (max-width: 768px) {
      width: 177px;
    }
  }
  @media screen and (max-width: 425px) {
    margin-bottom: 0;
    h5 {
      font-size: 18px;
      font-weight: 700;
    }
  }
`;

interface AssetCardProps {
  src: string;
  title: string;
  isVideoCard?: boolean;
  width?: string;
}

const AssetCard = ({ src, title, isVideoCard = false, width = '327px' }: AssetCardProps) => {
  const isMobile = useMedia('(max-width: 425px)');
  return (
    <CardWrapper className="asset-card" width={width}>
      {isVideoCard ? (
        <video autoPlay loop muted>
          <source src={src} type="video/mp4" />
          Your browser does not support the video tag.
        </video>
      ) : (
        <img
          className={'card-img ' + (title === 'MD License' ? 'md-license-image' : '')}
          src={src}
          alt={title}
        />
      )}

      {isMobile ? <div className="divider" /> : ''}
      <H5 fontWeight={700} className="card-name my-4">
        {title}
      </H5>
    </CardWrapper>
  );
};

export default AssetCard;
