const CollectRewardPage = () => {
  const isMobile = useMedia('(max-width: 768px)');
  const { lucid, accumulating, myWalletAddress } = useWalletConnect();
  const [showAccumlating, setShowAccumulating] = useState(false);
  const [alertMessage, setAlertMessage] = useState('');
  const [showLearnMoreModal, setShowLearnMoreModal] = useState(false);
  const [currentPartnerId, setCurrentPartnerId] = useState(0);
  const [isCardData, setIsCardData] = useState(false);

  const handleClick = async () => {
    if (!myWalletAddress) {
      setAlertMessage('Please connect your wallet first!');
      setShowAccumulating(true);
    } else if (!accumulating) {
      setAlertMessage('You are not accumulating!');
      setShowAccumulating(true);
    } else {
      const { data } = await axios.get(
        `https://655tardl56.execute-api.eu-west-2.amazonaws.com/default/MDCC-getTotalRewards?address=${myWalletAddress}`
      );

      if (data.rewards / 100000000 < 1) {
        setAlertMessage('You need to accumulate a minimum of 1 MD Token before collecting');
        setShowAccumulating(true);
        return;
      }
      if (lucid) {
        try {
          const tx = await lucid
            .newTx()
            .payToAddress('addr1vyyzfylquxnkfwm3xuwx2vfg9qajy6tne3qw8jzt6cfcpfs0vfydp', {
              lovelace: 5000000n
            })
            .complete();
          const signedTx = await tx.sign().complete();

          const txHash = await signedTx.submit();

          setAlertMessage('Transaction was successful');
          setShowAccumulating(true);

          console.log(txHash);
        } catch (err) {
          setAlertMessage('Transaction was unsuccessful');
          setShowAccumulating(true);
        }
      }
    }
  };

  return (
    <Wrapper className="page">
      <Container>
        <H2 fontWeight={700} textAlign="center">
          COLLECT REWARDS FROM
          <br /> YOUR ASSETS
        </H2>
        <SubtitleWrapper textAlign="center">
          <br />
          <br />
          You will only be able to collect rewards for all assets of MDCC and Partnership assets
          after beginning Accumulation.
          <br />
          <NavLink to="/accumulate">Click each project to learn more.</NavLink>
          <br />
          <br />
          <span className="small-bold">
            **Disclaimer: You will be only allow to collect rewards after accumulating 1 MD Token**
          </span>
          <br />
          <br />
        </SubtitleWrapper>
        <AssetsList className="first-assets-list mt-5">
          {cardData.map((item, index) => (
            <div key={index} className="partner-card d-flex flex-column align-item-center">
              <AssetCard
                src={item.image}
                isVideoCard={item.isVideo}
                title={item.title}
                width={'347px'}
              />
              <Button
                width="auto"
                padding="20px 58px"
                fontSize="32px"
                onClick={() => {
                  setShowLearnMoreModal(true);
                  setCurrentPartnerId(index);
                  setIsCardData(true);
                }}
              >
                LEARN MORE
              </Button>
            </div>
          ))}
        </AssetsList>
        <AssetsList className="assets-list-btn" marginBottom="150px">
          <Button onClick={handleClick} width="100%" padding="20px 75px" fontSize="32px">
            COLLECT REWARDS
          </Button>
        </AssetsList>
        <H2 textAlign="center" fontWeight={700} className="mb-4">
          PARTNERS
        </H2>
        <SubtitleWrapper textAlign="center" className="font-italic text-click">
          Click each project to learn more.
        </SubtitleWrapper>
        <br />
        <br />
        <AssetsList maxWidth={'900px'} marginBottom={'50px'}>
          {partnersData.map((partner, index) => (
            <div key={index} className="partner-card d-flex flex-column align-item-center">
              <AssetCard src={partner.image} title={partner.title} width={'347px'} />
              <Button
                width="auto"
                padding="20px 58px"
                fontSize="32px"
                onClick={() => {
                  setShowLearnMoreModal(true);
                  setCurrentPartnerId(index);
                  setIsCardData(false);
                }}
              >
                LEARN MORE
              </Button>
            </div>
          ))}
        </AssetsList>
      </Container>
      <AlertModal
        show={showAccumlating}
        onClose={() => setShowAccumulating(false)}
        message={alertMessage}
      />
      <LearnMoreModal
        show={showLearnMoreModal}
        onClose={() => setShowLearnMoreModal(false)}
        data={
          isCardData
            ? cardData[currentPartnerId].modalText
            : partnersData[currentPartnerId].modalText
        }
      />
    </Wrapper>
  );
};

export default CollectRewardPage;
