import styled from 'styled-components';
import { H6 } from '../../components/common/Typography';

interface ButtonStyle {
  width?: string;
  padding?: string;
  fontSize?: string;
}

interface AssetsListType {
  marginBottom?: string;
  maxWidth?: string;
  justifyContent?: string;
}

export const Wrapper = styled.div`
  padding: 275px 0;
  @media screen and (max-width: 991px) {
    padding: 142px 35px;
  }
  @media screen and (max-width: 425px) {
    h2 {
      font-size: 24px;
      margin-bottom: 0px;
    }
  }
`;

export const Container = styled.div`
  max-width: 1188px;
  margin: 0 auto;
  @media screen and (max-width: 768px) {
    padding-left: 36px;
    padding-right: 36px;
  }
`;

export const AssetsList = styled.div<AssetsListType>`
  max-width: ${(props) => props.maxWidth || '750px'};
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  margin-bottom: ${(props) => props.marginBottom};
  justify-content: ${(props) => props.justifyContent || 'space-between'};
  .partner-card {
    margin-bottom: 103px;
  }
  @media screen and (max-width: 768px) {
    justify-content: center;
  }
  @media screen and (max-width: 425px) {
    &.first-assets-list {
      margin-top: 20px !important;
    }
    &.assets-list-btn {
      margin-bottom: 75px;
    }
    .partner-card {
      margin-bottom: 75px;
    }
  }
`;

export const SubtitleWrapper = styled(H6)`
  margin: 0 auto;
  max-width: 947px;

  a {
    font-style: italic;
  }

  .btn-learn-more-collect {
    display: block;
    margin: auto;
    font-size: 24px;
    color: #ffffff;
    letter-spacing: 0.05em;
    font-style: italic;
    font-weight: 600;
  }
  .small-bold {
    font-size: 21px;
    font-weight: 600;
  }
  @media screen and (max-width: 425px) {
    font-size: 12px;
    &.text-click {
      margin-top: -12px;
    }
    .small-bold {
      font-size: 12px;
    }
  }
`;

export const Button = styled.button<ButtonStyle>`
  padding: ${(props) => props.padding || '10px 20px'};
  width: ${(props) => props.width || '100%'};
  margin: auto;
  background: #e2ae39;
  color: #000000;
  border-radius: 25px;
  text-align: center;
  font-size: ${(props) => props.fontSize || '24px'};
  font-weight: 700;
  border: none;
  transition: all 0.3s ease-in;

  &:hover {
    background: #8f8f8f;
    color: #ffffff;
  }

  @media screen and (max-width: 768px) {
    font-size: 18px;
    width: 237px;
    border-radius: 25px;
  }
  @media screen and (max-width: 425px) {
    padding: 19px;
  }
`;

export const ContentWrapper = styled.div`
  max-width: 1033px;
  margin: 64px auto;
  padding-top: 114px;

  .coming-soon {
    color: #ff0000;
  }

  ul {
    padding-left: 2.5rem;
  }

  ul li {
    font-size: 24px;
  }
`;
